import './switcher.scss';

const Switcher = ({isSwitcherOn = true, onText = 'must contain', offText = 'must not contain', toggle}) => (
  <div className='switcher'>
    <button 
      className='btn'
      onClick={toggle}
      title={onText}
    >
      {onText}
    </button>
    <button 
      className='btn off'
      onClick={toggle}
      title={offText}
    >
      {offText}
    </button>
    <div className={`slider ${!isSwitcherOn ? 'inactive' : ''}`}></div>
  </div>
);

export default Switcher;
