import './App.scss';
import { useState } from 'react';
import Switcher from './components/switcher/switcher.js';

function App() {
	
	const [isSwitcherOn, toggleSwitcher] = useState(true);

	const onToggle = () => {
		toggleSwitcher(!isSwitcherOn);
	};

	return (
		<div className="app">
		  <Switcher
		    isSwitcherOn={isSwitcherOn}
		    onText='is'
		    offText='is not'
		    toggle={onToggle}
		  />
		</div>
	);
}

export default App;
